import React from 'react';
import styles from '../style/index.module.sass';
import cl from 'classnames';
import RSelect from 'react-select';

type Props<T> = {
  value?: T;
  placeholder?: string;
  options?: Array<{ value: string; label: string }>;
  isErrored: boolean;
  onFocus: () => void;
  onBlur: () => void;
  onChange: (value: T) => Promise<T>;
};

const CommonInput = (props: Props<any>) => {
  return (
    <input
      className={cl({
        [styles.isErrored]: props.isErrored,
      })}
      type="text"
      defaultValue={props.value as any}
      onFocus={props.onFocus}
      onBlur={props.onBlur}
      onChange={(ev) => props.onChange(ev.target.value)}
    />
  );
};

const Select = (props: Props<string>) => {
  const customStyles = {
    control: (_: any, state: any) => ({
      display: 'flex',
      // ...provided,
      // none of react-select's styles are passed to <Control />
      // width: 200,
      background: 'none !important',
      borderBottom: `solid 0.13em ${state.isFocused ? '#5196D5' : '#ECEFEE'}`,
    }),
    option: (provided: any, state: any) => ({
      ...provided,
      borderBottom: '1px dotted pink',
      color: state.isSelected ? 'red' : 'blue',
      padding: 20,
    }),
    singleValue: (provided: any, state: any) => {
      const opacity = state.isDisabled ? 0.5 : 1;
      const transition = 'opacity 300ms';

      return { ...provided, opacity, transition };
    },
  };
  return <RSelect options={props.options} styles={customStyles} />;
};

const Input = (props: Props<any>) => {
  const { options } = props;
  if (options) return Select(props);
  return CommonInput(props);
};

export default Input;
